import React from "react";
import {
  Row,
  Col,
  TabContent,
  TabPane,
  Nav,
  NavItem,
  NavLink,
  Card,
  FormGroup,
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
  Input,
  Form,
  Alert,
  Button,
  CardHeader,
  CardBody,
  CardTitle,
  Media,
  Label,
} from "reactstrap";
import { toast } from "react-toastify";
// import {
//   Media,
//   Row,
//   Col,
//   Button,
//   Form,
//   Input,
//   Label,
//   FormGroup,
//   Table,
// } from "reactstrap";
import axios from "axios";
import "react-toastify/dist/ReactToastify.css";
import { ToastContainer } from "react-toastify";
import "../../../assets/scss/plugins/extensions/toastr.scss";
import avatar1 from "../../../assets/img/avatar/female.png";
import avatar2 from "../../../assets/img/avatar/male.png";
import CreatableSelect from "react-select/creatable";
import userImg from "../../../assets/img/portrait/small/avatar-s-18.jpg";
import Checkbox from "../../../components/@vuexy/checkbox/CheckboxesVuexy";
import "../../../assets/scss/plugins/extensions/dropzone.scss"
import { Check, Lock } from "react-feather";

const prefixOptions = [
  { value: "Mr", label: "Mr", isFixed: false },
  { value: "Miss", label: "Miss", isFixed: true },
  { value: "Mrs", label: "Mrs", isFixed: true },
  { value: "Dr", label: "Dr", isFixed: false },
];
const suffixOptions = [
  { value: "mr", label: "Mr", isFixed: true },
  { value: "miss", label: "Miss", isFixed: true },
  { value: "mrs", label: "Mrs", isFixed: true },
  { value: "dr", label: "Dr", isFixed: false },
];

class UserAccountTab extends React.Component {
  state = {
    showPass: false,
    active: "1",
    modal: false,
    saveButton : "Save Changes",
    firstname: "",
    salutation: JSON.parse(localStorage.getItem('logInUserData')).salutation ? JSON.parse(localStorage.getItem('logInUserData')).salutation : 'Mr',
    user: JSON.parse(localStorage.getItem('logInUserData')),
    mobile:JSON.parse(localStorage.getItem('logInUserData')).mobile,
    email: JSON.parse(localStorage.getItem('logInUserData')).email,
  };
  componentDidMount() {
    const user = JSON.parse(localStorage.getItem('logInUserData'));
    let names = user.name ? user.name.split(" ") : user.username.split(" ");
    let firstname="";
    let middlename="";
    let lastname="";
    if(names.length === 1) {
      firstname=names[0];
    } else if(names.length === 2) {
      firstname=names[0];
      lastname=names[1];
    } if(names.length === 3) {
      firstname=names[0];
      lastname=names[2];
      middlename=names[1];
    } if(names.length > 3) {
      firstname=names[0];
      for(let i=1; i < names.length - 1; i=i+1 ){
        middlename=middlename+" "+names[i]
      }
      lastname=names[2];
      middlename=names[names.length - 1];
    } 
    this.setState({
      firstname,middlename,lastname,
      emailPrimary:user.email,
      emailSecondary: user.secondaryemail ? user.secondaryemail : '',
      
    });
  }

  toggleModal = () => {
    this.setState((prevState) => ({
      modal: !prevState.modal,
      confirmPwd: "",
      showIdx: "",
    }));
  };

  toggleModal2 = () => {
   
      this.setState((prevState) => ({
        modal: !prevState.modal,
        confirmPwd: ""
      }));
  
      toast.warning("Something went wrong");
   
  }

  updateuser = (e) => {

    e.preventDefault()
    this.setState({saveButton: "Saving ..."})
    let {firstname,middlename,lastname , emailPrimary, mobile} = this.state;
    let user = JSON.parse(localStorage.getItem('logInUserData'));
    user.salutation = this.state.salutation;
    user.name = firstname+ ' ' +middlename+ ' ' +lastname;
    //user.email = emailPrimary;
    user.mobile = mobile;
    localStorage.setItem("logInUserData", JSON.stringify(user));
    axios
      .post("/backendapi/adddetails", user )
      .then((res) => {
        window.location.reload();
      })
      .catch();
  

    console.log(user);
  }

  render() {
    return (
      <div>
        <ToastContainer />
          <Modal
          isOpen={this.state.modal}
          toggle={this.toggleModal}
          className="modal-dialog-centered"
          fade={false}
        >
          <ModalHeader
            style={{ border: "solid 1px" }}
            toggle={this.toggleModal}
          >
           Change Passowrd
          </ModalHeader>
          <ModalBody style={{ border: "solid 1px" }}>
            Enter your current and new password 
            <br />
            <br />
            <FormGroup>
              {/* <Label for="password">Password:</Label> */}
              <Input
                type="password"
                id="password"
                placeholder="Current Passowrd"
                onChange={(e) => {
                  this.setState({ oldPwd: e.target.value });
                }}
              />
            </FormGroup>
            <FormGroup>
              {/* <Label for="password">Password:</Label> */}
              <Input
                type="password"
                id="password"
                placeholder="New Password"
                onChange={(e) => {
                  this.setState({ newPwd: e.target.value });
                }}
              />
            </FormGroup>
            <FormGroup>
              {/* <Label for="password">Password:</Label> */}
              <Input
                type="password"
                id="password"
                placeholder="Confirm Password"
                onChange={(e) => {
                  this.setState({ confirmPwd: e.target.value });
                }}
              />
            </FormGroup>
            <Alert className="mt-1" color="danger">
              <span>
                Please don't share your password with anyone
              </span>
            </Alert>
          </ModalBody>
          <ModalFooter>
            <Button
              color={this.state.confirmPwd === "" ? "primary" : "success"}
              disabled={this.state.confirmPwd === ""}
              onClick={this.toggleModal2}
            >
              Save
            </Button>{" "}
          </ModalFooter>
        </Modal>
      
      <Row>
        <Col sm="12">
          <Media className="mb-2">
            <Media className="mr-2 my-25" left href="#">
              <Media
                className="users-avatar-shadow rounded"
                object
                src={  JSON.parse(localStorage.getItem("logInUserData")) && JSON.parse(localStorage.getItem("logInUserData")).salutation === 'Mr'
                ? avatar2
                 : avatar1}
                alt="user profile image"
                height="84"
                width="84"
              />
            </Media>
            <Media className="mt-2" body>
              <Media className="font-medium-1 text-bold-600" tag="p" heading>
              {this.state.user.name}
              </Media>
              <div className="d-flex flex-wrap">
                <Button.Ripple className="mr-1" color="primary" outline onClick={this.toggleModal}>
                  Change Password
                </Button.Ripple>
               
              </div>
            </Media>
          </Media>
        </Col>
        <Col sm="12">
          <Form>
            <Row>
              <Col md="2" sm="11">
                <FormGroup >
                  <Label>Salutaion</Label>
                  <CreatableSelect
                    isMulti={false}
                    isClearable={false}
                    options={prefixOptions}
                    className="React"
                    classNamePrefix="select"
                    //defaultValue={prefixOptions[0]}
                    value={{ value: this.state.salutation, label: this.state.salutation, isFixed: false }}
                    onChange={(e) =>
                      this.setState({ salutation: e && e.value ? e.value : null })
                    }
                  />
                </FormGroup>
                </Col>
                <Col md="3" sm="11">
                <FormGroup>
                  <Label>First Name</Label>
                  <Input
                    type="text"
                    //placeholder="First Name"
                    required
                    value={this.state.firstname}
                    onChange={(e) =>
                      this.setState({ firstname: e.target.value })
                    }
                  />
                </FormGroup>
                </Col>
                <Col md="3" sm="11">
                <FormGroup>
                  <Label>Middle Name</Label>
                  <Input
                    type="text"
                    //placeholder="Middle Name"
                    //required
                    value={this.state.middlename}
                    onChange={(e) =>
                      this.setState({ middlename: e.target.value })
                    }
                  />
                </FormGroup>
                </Col>
                <Col md="3" sm="11">
                <FormGroup>
                  <Label>Last Name</Label>
                  <Input
                    type="text"
                    //placeholder="Last Name"
                    required
                    value={this.state.lastname}
                    onChange={(e) =>
                      this.setState({ lastname: e.target.value })
                    }
                  />
                </FormGroup>
              </Col>
                <Col md="4" sm="12">
                <FormGroup>
                  <Label>Primary Email</Label>
                  <Input
                    type="email"
                    //placeholder="Primary Email"
                    required
                    disabled={true}
                    value={this.state.emailPrimary}
                    // onChange={(e) =>
                    //   this.setState({ emailPrimary: e.target.value })
                    // }
                  />
                </FormGroup>
                </Col>
                <Col md="4" sm="12">
                <FormGroup>
                  <Label> Primary Contact</Label>
                  <Input
                    type="emailSecondary"
                   // placeholder="Secondary Email"
                    required
                    value={this.state.mobile}
                    onChange={(e) =>
                      this.setState({
                        mobile: e.target.value,
                      })
                    }
                  />
                </FormGroup>
              </Col>
             
              <Col
                className="d-flex justify-content-end flex-wrap mt-2"
                sm="12"
              >
                <Button.Ripple className="mr-1" color="primary" onClick={(e) => {this.updateuser(e)}}>
                {this.state.saveButton}
                </Button.Ripple>
                <Button.Ripple color="flat-warning">Reset</Button.Ripple>
              </Col>
            </Row>
          </Form>
        </Col>
      </Row>
      </div>
    );
  }
}
export default UserAccountTab;
